.next-lesson{
    display: block;
    padding: 18px 60px 18px 40px;
    background: #2d2d2d;
    color: var(--primary-highlight);
    font-family: 'Space Grotesk';
    font-weight: 500;
    font-size: 2rem;
    position: relative;
    max-width: 600px;
    margin: 0px auto;
    text-align: center;
    box-sizing: border-box;
    border-radius: 10px;
    width: 100%;
    cursor: pointer;

    @media (max-width: 600px){
      font-size: 1.5rem;
      width: 100%;
      padding: 18px 30px 18px 18px;
      min-width: unset;
    }

    a{
        text-decoration: none;
        color: var(--primary-highlight);

        img{
            position: absolute;
            top: 50%;
            right: 0px;
            transform: translate(50%, -50%);
        }
    }
}