.footer-container{
    width: 100%;
    background: #242424;
    // background: url('./background.png');
    background-size: contain;
    box-sizing: border-box;

    .footer{
        margin: 50px auto 0px;
        padding: 24px 48px 48px;
        font-family: 'ThicccBoi';
        width: 100%;
        max-width: 1440px;
        box-sizing: border-box;
        position: relative;
        display: flex;
        justify-content: space-between;

        @media (max-width: 600px){
            display: block;
            padding-top: 75px;
        }

        .logo{
            position: absolute;
            width: 8rem;
            top: 0px;
            left: 3rem;
            transform: translateY(-50%);
        }
        .left{
            align-self: flex-end;
            h4{
                font-size: 3rem;
                font-weight: 600;

                @media (max-width: 600px){
                    font-size: 1.5rem;
                }
            }
        
            p{
                font-weight: 500;
                font-size: 1.1rem;
                line-height: 2rem;

                @media (max-width: 600px){
                    font-size: 1rem;
                    line-height: unset;
                }
            }
            .amfi{
                display: inline-block;
                margin-left: 12px;
                p{
                    font-size: 0.78rem;
                    margin: 0.3rem 0px 0.3rem;
                    letter-spacing: 0.03rem;
                    line-height: unset;
                    font-weight: 300;

                    &:nth-child(3){
                        font-weight: 600;
                    }
                }
            }
        }
        .right{
            text-align: right;
            align-self: flex-end;

            @media (max-width: 600px){
                text-align: left;
            }
            h1{
                font-size: 2rem;
                font-weight: 500;
                margin-bottom: 0.5rem;

                @media (max-width: 600px){
                    font-size: 1.5rem;
                    margin-top: 4rem;
                }
            }
            img{
                width: 12rem;
                margin-bottom: 2rem;

                @media (max-width: 600px){
                    width: 12rem;
                    margin-bottom: 0rem;
                }
            }
            p{
                font-size: 1.2rem;
                font-weight: 500;
                line-height: 2.5rem;

                @media (max-width: 600px){
                    font-size: 1rem;
                    line-height: 1.5rem;
                }
            }
            .copyright{
                opacity: 0.5;
                margin: 0px;
            }
        }
    }
}