.mutual-fund{
    background: #202020;
    border-radius: 6px;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    margin-bottom: 10px;

    .header{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        box-sizing: border-box;
        
        .icon{
            width: 40px;
        }
        
        .check{
            display: block;
            border-radius: 10px;
            border-style: solid;
            border-width: 1px;
            border-color: white;

            img{
                opacity: 0;
                width: 100%;
                transition-duration: 100ms;
            }

            &.selected{
                img{
                    opacity: 1;
                }
            }
        }

        .description{
            width: 100%;
            padding: 0px 10px;
            .fund-name{
                font-family: 'DM Sans';
                font-size: 12px;
            }

            .tags{
                .tag{
                    font-family: 'DM Sans';
                    border-radius: 2px;
                    display: inline-block;
                    padding: 3px 8px 4px;
                    font-size: 10px;
                    margin-right: 5px;
                    font-weight: 700;
                }

            }
        }
    }
    .details{
        background: #303030;
        border-radius: 4px;
        display: flex;
        justify-content: space-between;
        padding: 8px;
        margin-top: 8px;

        .detail{
            .label{
                font-family: 'DM Sans';
                font-weight: bold;
                font-size: 10px;
                text-align: center;
                color: rgba(255,255,255,0.5);
            }
            .value{
                margin-top: 6px;
                font-family: 'DM Sans';
                font-weight: 400;
                font-size: 14px;
                line-height: 18px;
                text-align: center;
            }
        }
    }
}