.nav-bar{
    width: 100vw;
    background: green;
    position: fixed;
    top: 0px;
    left: 0px;
    background: var(--background);
    border-width: 0px 0px 1px 0px;
    border-color: #313131;
    border-style: solid;
    box-sizing: border-box;
    z-index: 5;
    
    .header{
        width: 85%;
        max-width: 1800px;
        margin: 36px auto;
        display: flex;
        justify-content: space-between;

        @media (max-width: 600px) {
            width: 90%;
            margin: 20px auto;
        }

        div{
            font-family: 'DM Sans';
            a{
                img{
                    margin-left: 10px;

                    @media (max-width: 600px) {
                        height: 24px;
                        margin-left: 10px;
                    }
                }

                &:nth-child(1){
                    margin-left: 0px;
                    margin-right: 30px;

                    @media (max-width: 600px) {
                        margin-right: 10px;
                    }
                }

                &:nth-child(2){

                    img{
                        width: 80px;
                    }
                }
            }
        }

        img{
            vertical-align: middle;
        }

    }

    .menu{
        height: 0px;
        transition-duration: 0.5s;
        overflow: hidden;
        width: 85%;
        max-width: 1800px;
        margin: auto;
        &.open{
            height: calc( 100vh - 63px);
        }

        a{
            display: block;
            color: white;
            text-decoration: none;
            font-family: 'DM Sans';
            font-size: 2rem;
            margin: 48px 24px;

            @media (max-width: 600px) {
                font-size: 1.5rem;
                margin: 48px 12px;
            }
        }
    }
}