.email-sub{
    width: 100%;
    max-width: 600px;
    font-family: 'ThicccBoi';
    font-size: 1.2rem;
    margin: auto;
    margin-top: 100px;
    text-align: left;
    
    form{
        width: 100%;
        background: white;
        padding: 10px 10px 10px 30px;
        box-sizing: border-box;
        border-radius: 10px;
        display: flex;

        input[type='email']{
            width: calc(100% - 80px);
            outline: none;
            background: transparent;
            border: none;
            font-size: 1.2rem;
            font-family: 'ThicccBoi';
            color: #333;
        }

        input[type='submit']{
            color: green;
            outline: none;
            background: #008afc;
            font-family: 'ThicccBoi';
            border: none;
            border-radius: 10px;
            box-shadow: 0px 2px 16px 0px #008afc80;
            padding: 12px 0px;
            color: white;
            font-weight: 600;
            font-size: 1rem;
            width: 80px;
            cursor: pointer;
        }
    }
}