.playstore-badge{
    width: 100%;
    max-width: 600px;
    box-sizing: border-box;
    background: linear-gradient(135deg, rgba(0,138,252,1) 0%, rgba(0,138,252,1) 70%, rgba(0,71,204,1) 71%, rgba(0,71,204,1) 100%);
    padding: 10px 10px 10px 30px;
    border-radius: 10px;
    margin: 30px auto 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: left;

    @media (max-width: 600px) {
        padding: 10px 10px 10px 20px;
    }

    p.badge{
        width: calc(100% - 153px);
        font-size: 1.2rem;
        font-family: 'ThicccBoi';
        color: white;
        margin: 0px;
    
        @media (max-width: 600px) {
            width: calc(100% - 114px);
        }
    }

    img.badge{
        height: 48px;
        width: 153px;
        object-fit: contain;
        cursor: pointer;
    
        @media (max-width: 600px) {
            height: 36px;
            width: 114px;
        }
    }
}
