.index{
    background: var(--background);
    z-index: 3;
    overflow: hidden;
    transition-duration: 1s;
    box-sizing: border-box;
    display: inline-block;
    width: 32.5%;
    vertical-align: top;

    .next-lesson{
        color: var(--primary-highlight);
        margin-top: 40px;
        h2{
            font-family: 'Space Grotesk';
            font-size: 4rem;
            font-weight: 500;
            margin: 0rem;
            padding: 0rem;
            position: relative;
            top: 1.5rem;
            opacity: 0.2;
        }
        div{
            cursor: pointer;
            width: 100%;
            border-radius: 16px;
            position: relative;
            background: #2d2d2d;
            text-align: center;

            img{
                width: 100%;
                border-radius: 16px;
            }
            h1{
                font-family: 'Space Grotesk';
                font-size: 2rem;
                font-weight: 500;
                margin: 0rem;
                padding: 0.5rem 0.8rem;
            }
        }
    }

    @media (max-width: 1080px) {
        position: fixed;
        top: 0px;
        width: 100vw;
        height: 100vh;
        transform: translateY(0%);
        z-index: 5;

        &.closed{
            transform: translateY(100%);
        }
    }
    .nav{
        position: fixed;
        display: none;
        bottom: 0px;
        background: #282828;
        height: 68px;
        width: 100%;
        box-sizing: border-box;
        padding: 10px;
        text-align: right;
        // display: flex;
        justify-content: space-between;

        @media (max-width: 1080px){
            display: flex;
        }

        .back{
            width: 50%;
            padding: 15px 0px;
            display: inline-block;
            font-family: 'DM Sans';
            font-weight: 900;
            font-size: 1rem;
            color: #FFFFFF80;
            text-align: center;
        }

        .next{
            width: 50%;
            text-align: center;
            background: #FF6534;
            padding: 15px 0px;
            display: inline-block;
            margin-left: 10px;
            font-family: 'DM Sans';
            font-weight: 900;
            font-size: 1rem;
            border-radius: 3px;
        }
    }

    .index-container{
        width: 100%;
        padding: 30px;
        background: #232323;
        border-radius: 20px;
        box-sizing: border-box;


        @media (max-width: 1080px){
            max-width: 800px;
            height: calc(100vh - 68px);
            overflow: scroll;
            margin: auto;
            padding: 50px 0px 0px;
            max-width: unset;
            border-radius: 0px;
        }
        .header{
            width: 100%;

            @media (max-width: 1080px) {
                margin-left: 7%;
                width: 85%;
            }

            .sub-title{
                color: var(--secondary-highlight);
                font-size: 0.9rem;
                font-family: 'ThicccBoi';
                font-weight: 600;
                letter-spacing: 6px;
                margin: 0px 0px 12px 0px;
            }

            .title-box{
                width: 100%;
                overflow: hidden;
                margin-bottom: 18px;
                .container{
                    width: 200%;
                    h3{
                        display: inline-block;
                        font-family: 'Space Grotesk';
                        font-size: 2rem;
                        color: var(--primary-highlight);
                        margin: 0px 18px 0px 0px;
                        vertical-align: middle;
                        font-weight: 400;

                    }
                    .title-line{
                        display: inline-block;
                        height: 1px;
                        width: 50%;
                        background: var(--text-color);
                        vertical-align: middle;
                    }
                }
            }
        }

        .list{
            width: 85%;
            margin: auto;
            .lesson{
                margin-bottom: 50px;
                .arrow{
                    vertical-align: middle;
                    transform: scaleY(-100%);
                    float: right;
                }
                .title{
                    .dot{
                        display: inline-block;
                        height: 18px;
                        width: 18px;
                        border-radius: 10px;
                        border-style: solid;
                        border-width: 1px;
                        margin-right: 10px;
                        vertical-align: middle;
                        overflow: hidden;
                        padding: 2px;
                        box-sizing: border-box;
                        cursor: pointer;
                        border-color: var(--secondary-highlight);
                        div{
                            background: var(--primary-highlight);
                            width: 100%;
                            height: 100%;
                            border-radius: 10px;
                        }
                    }
                    span{
                        cursor: pointer;
                        font-family: 'DM Sans';
                        color: var(--secondary-highlight);
                        font-weight: 900;
                        letter-spacing: 0.2rem;
                        font-size: 0.8rem;
                        text-transform: uppercase;
                    }
                }
                .bend{
                    line-height: 0px;
                    opacity: 1;
                    transition-duration: 0.5s;
                    .left{
                        width: 9px;
                        display: inline-block;
                        height: 9px;
                        border-width: 0px 0px 1px 1px;
                        border-radius: 0px 0px 0px 5px;
                        border-style: solid;
                        border-color: var(--secondary-highlight);
                        margin-left: 8.5px;
                        vertical-align: top;
                    }
                    .right{
                        vertical-align: bottom;
                        width: 9px;
                        display: inline-block;
                        height: 4px;
                        border-width: 1px 1px 0px 0px;
                        border-radius: 0px 5px 0px 0px;
                        border-style: solid;
                        border-color: var(--secondary-highlight);
                        margin-top: 9px;
                    }
                }
                &.collapsed{
                    .chapters{
                        max-height: 0vw;
                    }
                    .bend{
                        opacity: 0;
                    }
                    .arrow{
                        transform: scaleY(100%);
                    }
                }
                .chapters{
                    overflow-y: hidden;
                    transition-duration: 0.5s;
                    cursor: pointer;

                    .chapter{
                        margin-left: 28px;
                        padding: 10px 0px 2px 18px;
                        position: relative;
                        
                        .chapter-line{
                            width: 1px;
                            height: 32px;
                            background: var(--secondary-highlight);
                            position: absolute;
                            top: 0px;
                            left: -0.5px;
                        }
                        .chapter-dot{
                            width: 9px;
                            height: 9px;
                            border-radius: 8px;
                            border-width: 3px;
                            background: white;
                            position: absolute;
                            top: 13px;
                            left: -7.5px;
                            border-style: solid;
                            border-color: var(--primary-highlight);
                            background: var(--primary-highlight);
                            transition-duration: 250ms;

                            img{
                                opacity: 0;
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translate(-50%, -43%);
                            }
                        }

                        .here{
                            display: none;
                            padding: 8px 10px;
                            border-radius: 20px;
                            background: #FF653430;
                            margin: 10px 0px;
                            font-size: 0.9rem;
                        }

                        &.reading{
                            .chapter-dot{
                                border-color: var(--primary-highlight);
                                background: white;
                            }

                            .here{
                                display: inline-block;
                            }
                        }

                        &.read{
                            .chapter-dot{
                                border-color: white;
                                background: white;
                                transition-duration: 250ms;

                                img{
                                    opacity: 1;
                                }
                            }
                        }

                        span{
                            font-size: 1rem;
                            font-family: 'DM Sans';
                        }
                    }
                }
            }
        }
    }
}