.post-page{
    width: 90%;
    max-width: 1440px;
    margin: 120px auto;
    vertical-align: top;
    display: flex;
    justify-content: space-between;

    @media (max-width: 1080px) {
        display: block;
        width: 100%;
    }

    @media (max-width: 1080px) {
        margin: 0px auto;
    }

    .post-view{
        position: relative;
        z-index: 1;
        width: 60%;
        margin-bottom: 68px;
        display: inline-block;
        text-align: left;

        @media (max-width: 1080px) {
            width: 90%;
            margin: auto;
            display: block;
        }

        .next-container{
            width: 100%;
            max-width: 600px;
            margin: auto;

            h3{
                font-family: 'Space Grotesk';
                font-size: 2rem;
                margin: 0px 0px 0px 1rem;
                height: 1.9rem;
                font-weight: 400;
                color: var(--primary-highlight);
                opacity: 0.2;
            }
        }
    }

    .post-nav{
        position: fixed;
        bottom: 0px;
        z-index: 2;
        box-shadow: 0px -4px 16px 0px #0000005A;
        width: 100%;
        height: 68px;
        background: var(--background-secondary);
        display: none;
        justify-content: space-between;
        padding: 16px;
        box-sizing: border-box;

        @media (max-width: 1080px) {
            width: 100%;
            display: flex;
        }

        .current-path{
            display: inline-block;
            text-align: center;

            h5{
                margin: 0px;
                margin-bottom: 8px;
                font-size: 8px;
                font-family: 'ThicccBoi';
                font-weight: 600;
                color: var(--secondary-highlight);
                letter-spacing: 2px;
                text-transform: uppercase;
            }
            h4{
                margin: 0px;
                font-size: 12px;
                font-family: 'DM Sans';
                font-weight: 400;
            }
        }
    }
}